"use strict";
Component({
    options: {
        addGlobalClass: true,
        multipleSlots: true,
    },
    properties: {
        address: {
            type: Object,
            value: {},
        },
        customIcon: {
            type: String,
            value: 'edit-1',
        },
        extraSpace: {
            type: Boolean,
            value: true,
        },
        isDrawLine: {
            type: Boolean,
            value: true,
        },
    },
    externalClasses: [
        'item-wrapper-class',
        'title-class',
        'default-tag-class',
        'normal-tag-class',
        'address-info-class',
        'delete-class',
    ],
    methods: {
        onDelete: function (e) {
            var item = e.currentTarget.dataset.item;
            this.triggerEvent('onDelete', item);
        },
        onSelect: function (e) {
            var item = e.currentTarget.dataset.item;
            this.triggerEvent('onSelect', item);
        },
        onEdit: function (e) {
            var item = e.currentTarget.dataset.item;
            this.triggerEvent('onEdit', item);
        },
    },
});
